import React, { useRef, useState } from "react";
import CloseOutlinedIcon from '@mui/icons-material/CloseOutlined';


export default function FormCollect(prop){


  const Fdata = useRef()

  const [details,setDetails] = useState(
    {
      name:"",
      gotram:"",
      date:"",
      time:"",
      contact:"",
      email:""
    }
  )

  function handleChange(e){
    e.preventDefault();

    const data = {...details};

    data[e.target.id] = e.target.value;
    setDetails(data);
    
    

    // console.log("details:",details)

  }

    function handleSubmit(e){

      e.preventDefault();
      console.log(details);

      

    }




    return(<>
      
        <div style={{ color:'white', padding:'30px 0px'}}>
           <form onSubmit={handleSubmit} style={{padding:'30px'}}>
           <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">{prop.question}</h5>
                                <button style={{border:"none", background:'none'}} type="button" className="" data-bs-dismiss="modal" aria-label="Close"><CloseOutlinedIcon style={{color:'red',background:"none"}}/></button>
                            </div>

   <div className="form-group">
    <label for="exampleInputPassword1">Full Name *</label>
    <input id="name" name="name" type="text"  className="form-control"  placeholder="Full Name"  onChange={handleChange} value={details.name}/>
  </div>
  <div className="form-group">
    <label for="exampleInputPassword1">Gotram *</label>
    <input id="gotram" name="gotram" type="text" className="form-control"  placeholder="Gotram" onChange={handleChange} value={details.gotram}/>
  </div>
  <div className="form-group">
    <label for="exampleInputPassword1">Birth Date *</label>
    <input id="date"  name="date" type="date" className="form-control" placeholder="Birth date" onChange={handleChange} value={details.date} />
  </div>
  <div className="form-group">
    <label for="exampleInputPassword1">Birth Time *</label>
    <input id="time" name="time" type="time" className="form-control"  placeholder="Birth time" onChange={handleChange} value={details.time}/>
  </div>
 
  {/* <div className="form-group">
    <label for="exampleInputPassword1">Question *</label>
    <textarea type="text" className="form-control" id="exampleInputPassword1" placeholder="Please enter Your Question" />
  </div> */}
  <div className="form-group">
    <label for="exampleInputPassword1">Contact Number *</label>
    <input id="contact" name="contact" type="number" className="form-control"  placeholder="Mobile Number" onChange={handleChange} value={details.contact}/>
  </div>
  <div className="form-group" >
    <label for="exampleInputEmail1">Email address (optional)</label>
    <input id="email" name="email" type="email" className="form-control"  aria-describedby="emailHelp" placeholder="Enter email" onChange={handleChange} value={details.email} />
    <small id="emailHelp" className="form-text " style={{color:'grey'}}>We'll never share your email with anyone else.</small>
  </div>
  <button type="submit" className="btn btn-success" style={{marginTop:'20px'}}>మీరు సమాధానం తెలుసుకోవాలనుకుంటున్నారా</button>
</form>
        </div>
        </>
    )
}